import React, { useState, useEffect } from 'react';
import querystring from "querystring"

function Confirm() {

    const [data, setData] = useState("");

    useEffect(()=> {
        var dataB64 = querystring.parse(window.location.search.split('?')[1])["data"];
        var data = JSON.parse(atob(dataB64));
        setData(JSON.stringify(data,4, null))
    }, [])

    return (
        <div>
            <div>Immune</div>
            <div>{data}</div>
        </div>
    );

}

export default Confirm;