import qrcode from "qrcode"
import querystring from "querystring"

import React, { useState, useEffect } from 'react';

function Image(props) {

    const qrOption = {
        margin: 2,
        width: 175
    }

    const [image, setImage] = useState("data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==");

    useEffect(async () => {
        if(props.data) {
            const bufferImage = await qrcode.toDataURL(props.data,qrOption);
            setImage(bufferImage);
        }
    }, [props.data]);
    

    return (
        <img src={image} />
    );

}

function View() {

    const [url, setUrl] = useState("");
    const [data, setData] = useState({});

    useEffect(()=> {
        var dataB64 = querystring.parse(window.location.search.split('?')[1])["data"];
        var dataObj = JSON.parse(atob(dataB64));
        var url = `${window.location.origin}/${window.location.pathname.split('/').slice(1, -1).join('/')}/confirm?data=${dataB64}`;
        console.log(dataObj);
        setData({...dataB64, ...dataObj});
        setUrl(url);
    }, [])

    return (
        <div>
            <div>View</div>
            <Image data={url}/>
        </div>

    );
}

export default View;
export {Image};